@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --c1: black;
  --c2: #155f4e;
  --c3: rgb(241, 255, 231);
  --c2_1: #0baa85;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: rgb(241, 255, 231);
  background-color: var(--c3);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-bar{position:absolute;width:100%;height:60px;display:flex;flex-direction:row;align-items:center;justify-content:flex-end;font-size:13px;z-index:99}.nav-bar .nav-link{padding:10px 20px}.nav-bar .nav-link:last-child{margin-right:150px}.nav-bar .nav-link.name-logo{display:-ms-grid;display:grid;place-items:center;margin-right:auto;padding-left:10px;padding-right:30px;background-color:var(--c3);border-top-right-radius:30px;border-bottom-right-radius:30px}.nav-bar .nav-link.name-logo a{color:var(--c2);font-size:20px;font-weight:bold}.nav-bar .nav-link.name-logo a .fname{color:var(--c1)}.nav-bar .nav-link.name-logo a::before{all:unset}.nav-bar .nav-link a{--color: var(--c3);position:relative;color:var(--color);text-decoration:none}.nav-bar .nav-link a::before{position:absolute;content:"";background-color:var(--color);height:2px;width:100%;bottom:-2px;transform:scaleX(0);transition:transform 200ms ease-in-out}.nav-bar .nav-link a:not(logo):hover::before,.nav-bar .nav-link a.active::before{transform:scaleX(1)}.nav-bar .nav-link:has(a.active){border:1px solid black}.nav-bar.vertical{position:fixed;top:0%;left:10px;flex-direction:column;align-items:center;justify-content:center;top:50%;transform:translateY(-50%);height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;border-radius:50px;border:1px solid #e2e1e1;padding:0px;background-color:unset}.nav-bar.vertical .nav-link{position:relative;display:flex;flex-direction:row}.nav-bar.vertical .nav-link.name-logo{display:none}.nav-bar.vertical .nav-link:last-child{margin-right:0px}.nav-bar.vertical .nav-link svg{cursor:pointer}.nav-bar.vertical .nav-link a:nth-child(1) svg{--size: 18px;width:var(--size);height:var(--size);fill:var(--c1)}.nav-bar.vertical .nav-link a:nth-child(1):hover ~ a:nth-child(2){transform:scale(1);opacity:1}.nav-bar.vertical .nav-link a:nth-child(2){position:absolute;left:100%;width:110px;border-radius:15px;border-bottom-left-radius:0;background-color:var(--c3);box-shadow:1px 1px 3px var(--c2);color:var(--c1);padding:5px;transform:scale(0);transform-origin:left center;transition:transform 200ms ease-in-out;opacity:0}.nav-bar.vertical .nav-link a::before{all:unset}

