dding-left:20px;padding-right:20px}.home-page .left-card .sub-title{color:var(--c1)}.home-page .left-card .name{font-size:25px;white-space:pre;display:flex;flex-direction:row}.home-page .left-card .name span{display:inline-block}.home-page .left-card .name span.capital{font-size:30px;color:var(--c2)}.home-page .left-card .name span:hover{-webkit-animation-delay:0 !important;animation-delay:0 !important;-webkit-animation:stumble 300ms ease-in-out 1 alternate;animation:stumble 300ms ease-in-out 1 alternate}.home-page .left-card .social-links{display:flex}.home-page .left-card .social-links div{margin:0 5px}.home-page .left-card .social-links div .MuiSvgIcon-root{cursor:pointer;--size: 26px;width:var(--size);height:var(--size);fill:var(--c1)}.home-page .left-card .social-links div .MuiSvgIcon-root:hover{-webkit-animation:stumble 300ms ease-in-out 1 alternate;animation:stumble 300ms ease-in-out 1 alternate}

